<template>
  <div class="workList">

    <el-table :data="tableData" v-loading="loading" border style="width: 100%">
      <el-table-column prop="id" label="用户ID"> </el-table-column>
      <el-table-column prop="userId" label="所属班级" width="180"> </el-table-column>
      <el-table-column prop="title" label="作业名称" :formatter="colFormatter"> </el-table-column>
      <el-table-column prop="completed" label="完成情况" :formatter="colFormatter"> </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page=currentPage
      :page-sizes="[10, 20, 50, 100]"
      :page-size=pageSize
      layout="total, sizes, prev, pager, next, jumper"
      :total= total>
    </el-pagination>

    
  </div>
</template>

<script>
import {getTableData} from '@/utils/table'

export default {
  data() {
    return {
      total: 0, //数据总条数
      currentPage: 1,   //当前页码
      pageSize: 10, //每页条数  
      
      loading: true,
      
      tableData: [],
      pageInfo: {
        page: 1,
        size: 10
      }
      
    }
  },

  created() {
    this.getData();
  },

  methods: {
    getData() {
      this.loading = true;
      this.pageInfo.page = this.currentPage;
      this.pageInfo.size = this.pageSize;
      getTableData(this, '/works', this.pageInfo) 
      // await new Promise(resolve => setTimeout(resolve, 500));
      // this.loading = false; 
    },

    //统一字典处理 (可以所有列都加上，通过column.property来判断具体字段)，通过后台获取所有字典，判断该字段是否在字典中
    colFormatter(row, column, cellValue, index) {
      // console.log(column.property)

      if(column.property == "completed"){
        let wcqk = ""
        if(cellValue){
          wcqk = "已完成"
        }else {
          wcqk = "未完成"
        }
        return wcqk
      }else {
        return cellValue;
      }
      

    },

    //分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pageSize = val;
      this.currentPage = 1;
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val;
      this.getData();
    },
  }

  

}
</script>

<style lang="scss" scoped>
  .workList {
    .el-form-item {
      text-align: left;
    }
    .el-pagination {
      text-align: left;
      margin-top: 20px;
    }

  }
</style>